import React, { useState } from 'react';

function HelpDropdownList() {
  const [selectedSection, setSelectedSection] = useState(null);
  const [selectedOption, setSelectedOption] = useState(null);

const option_admin_dodaj_linie = <p className='contentText'><ol>
  <li>1. Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie liniami.</li>
  <li>2. Pojawi si panel Linie, w ktrym po prawej stronie wida aktualn list linii.</li>
  <li>3. W okienku po lewej stronie moesz wpisa nazw linii oraz okreli jej typ (1-normalna, 2-nocna, 3-metropolitalna, 4-zastepcza).</li>
  <li>4. Zakocz naciskajc przycisk Dodaj</li>
  <li>5. Dodana linia powinna wywietli si na licie po prawej stronie.</li>
</ol></p>

const option_admin_usun_linie = <p classname='contentText'><ol>
  <li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie liniami.</li>
  <li>2.	Pojawi si panel Linie, w ktrym po prawej stronie wida aktualn list linii.</li>
  <li>3.	Klikajc przycisk Delete w kolumnie Akcje moesz usun dan lini.</li>
</ol></p>

const option_admin_edytuj_linie = <p classname='contentText'><ol>
  <li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie liniami.</li>
  <li>2.	Pojawi si panel Linie, w ktrym po prawej stronie wida aktualn list linii.</li>
  <li>3.	Klikajc przycisk Edit w kolumnie Moliwe moesz edytowa przystanki nalece do danej linii.</li>
  <li>4.	Po klikniciu tego przycisku otwiera nam si panel edycji linii, w ktrym wida jakie przystanki nale do danej linii.</li>
  <li>5.	Po lewej stronie moesz wpisa do okienka pod Identyfikator przystanku jaki przystanek chcesz doda, a nastpnie klikn przycisk Dodaj przystanek dla linii, by pojawi si na licie obok.</li>
  <li>6.	Po prawej stronie na licie przystankw w linii mona usuwa przystanki klikajc przycisk Delete obok danego przystanku.</li>
</ol></p>

const option_admin_dodaj_autobus = <p classname='contentText'><ol>
  <li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie kursami.</li>
  <li>2.	Pojawi si panel Autobusy, w ktrym po lewej stronie wida aktualn list autobusw oraz szczegy ich dotyczce.</li>
  <li>3.	Kliknij przycisk dodaj autobus znajdujcy si po prawej stronie.</li>
  <li>4.	Pojawi si panel dodawania autobusu, w ktrym w odpowiednich polach wpisz jego mark, rejestracj i rodzaj (1-normalny, 2-niskopodogowy, 3-elektryczny), dodaj informacj na temat ostatniego i kolejnego przegldu oraz okrel jego sprawno.</li>
  <li>5.	Potwierd naciskajc przycisk Zapisz</li>
  <li>6.	Po klikniciu przycisku Powrt w prawym grnym rogu powiniene przej z powrotem do panelu Autobusy, w ktrym nowo dodany autobus powinien pojawi si na licie po lewej stronie.</li>
</ol></p>

const option_admin_usun_autobus = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie autobusami.</li>
<li>2.	Pojawi si panel Autobusy, w ktrym po lewej stronie wida aktualn list autobusw oraz szczegy ich dotyczce.</li>
<li>3.	Klikajc przycisk Delete w kolumnie Delete moesz usun dany autobus.</li>
</ol></p>

const option_admin_dodaj_kurs = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie kursami.</li>
<li>2.	Pojawi si panel kursw, w ktrym po lewej stronie wida aktualn list kursw oraz szczegy ich dotyczce.</li>
<li>3.	W okienku po prawej stronie Dodaj kurs wpisz nazw linii, rodzaj autobusu oraz czas odjazdu.</li>
<li>4.	Zakocz naciskajc przycisk Dodaj.</li>
<li>5.	Dodany kurs powinien pojawi si na licie po lewej stronie.</li>
</ol></p>

const option_admin_usun_kurs = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie kursami.</li>
<li>2.	Pojawi si panel Kursy, w ktrym po lewej stronie wida aktualn list kursw oraz szczegy ich dotyczce.</li>
<li>3.	Klikajc przycisk Delete w kolumnie Delete moesz usun dany kurs.</li>
</ol></p>

const option_admin_dodaj_przystanek = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie przystankami.</li>
<li>2.	Pojawi si panel przystankw, w ktrym po lewej stronie wida aktualn przystankw.</li>
<li>3.	W okienku po prawej stronie wpisz nazw przystanku, ktry chcesz doda.</li>
<li>4.	Zakocz naciskajc przycisk Dodaj przystanek.</li>
<li>5.	Dodany przystanek powinien pojawi si na licie po lewej stronie.</li>
</ol></p>

const option_admin_usun_przystanek = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie przystankami.</li>
<li>2.	Pojawi si panel Przystanki, w ktrym po lewej stronie wida aktualn list kursw oraz szczegy ich dotyczce.</li>
<li>3.	Klikajc przycisk Delete w kolumnie Delete moesz usun dany kurs.</li>
</ol></p>

const option_admin_dodaj_uzytkownika = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie uytkownikami.</li>
<li>2.	Pojawi si panel Uytkownicy, w ktrym po lewej stronie wida aktualn list uytkownikw oraz szczegy ich dotyczce.</li>
<li>3.	Kliknij przycisk dodaj uytkownika znajdujcy si po prawej stronie.</li>
<li>4.	Pojawi si panel dodawania uytkownika, w ktrym w odpowiednich polach wpisz jego imi, nazwisko, stanowisko(1-administrator, 2-dyspozytor, 3-kierowca), ulic, miasto oraz wiek.</li>
<li>5.	Potwierd naciskajc przycisk Dodaj</li>
<li>6.	Po klikniciu przycisku Powrt w prawym grnym rogu powiniene przej z powrotem do panelu Uytkownicy, w ktrym nowo dodany uytkownik powinien pojawi si na licie po lewej stronie.</li>
</ol></p>

const option_admin_usun_uzytkownika = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym administratora kliknij przycisk Zarzdzanie uytkownikami.</li>
<li>2.	Pojawi si panel Uytkownicy, w ktrym po lewej stronie wida aktualn list kursw oraz szczegy ich dotyczce.</li>
<li>3.	Klikajc przycisk Delete w kolumnie Delete moesz usun danego uytkownika.</li>
</ol></p>

const option_kierowca_dyspozycja = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym kierowcy kliknij przycisk Dyspozycja.</li>
<li>2.	Pojawi si panel Kierowca z rubrykami do wpisywania dyspozycji.</li>
<li>3.	W odpowiednich polach wpisz swj identyfikator, wybierz dzie (1-poniedziaek, 2-wtorek, 3-roda, 4-czwartek, 5-pitek) i rodzaj zmiany, ktry ci odpowiada.</li>
<li>4.	Zakocz naciskajc przycisk Dodaj zmiane.</li>
</ol></p>

const option_kierowca_grafik = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym kliknij przycisk Sprawd grafik.</li>
<li>2.	Pojawi si panel Kierowca z tabelk, za pomoc ktrej moesz odczyta w jakie dni i na jak zmian zostae przypisany.</li>
</ol></p>

const option_dyspozytor_dodaj_realizacje = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym kliknij przycisk Edycja kursw.</li>
<li>2.	Pojawi si panel Zarzdzanie kursami, w ktrym wida aktualna list kursw.</li>
<li>3.	Poniej w sekcji Zrealizuj kurs wybierz w jakim dniu chcesz doda now realizacj kursu za pomoc list rozwijanych i przyporzdkuj mu kierowc oraz autobus.</li>
<li>4.	Zakocz naciskajc przycisk Dodaj</li>
<li>5.	Dodany kurs powinien wywietli si na licie powyej. </li>
</ol></p>   

const option_dyspozytor_usun_realizacje = <p classname='contentText'><ol>
<li>1.	Po zalogowaniu w panelu gwnym kliknij przycisk Edycja kursw.</li>
<li>2.	Pojawi si panel Zarzdzanie kursami, w ktrym wida aktualna list kursw.</li>
<li>3.	Klikajc przycisk Delete w kolumnie Delete moesz usun dan realizacj kursu.</li>
</ol></p>    

const option_uzytkownik_lista_przystankow = <p classname='contentText'><ol>
<li>1.	Po klikniciu w przycisk Nasze przystanki wywietla si aktualna lista przystankw.</li>
</ol></p>    

const option_uzytkownik_lista_linii = <p classname='contentText'><ol>
<li>1.	Po klikniciu w przycisk Nasze linie wywietla si aktualna lista linii.</li>
</ol></p>    

  const sections = [    
    {name: 'Uytkownik',      
    options: [        
        { 
            name: 'Zobacz list przystankw',          
            descriptions: [option_uzytkownik_lista_przystankow]
        },
        {
          name: 'Zobacz list linii',
          descriptions: [option_uzytkownik_lista_linii]
        }
      ]
    },
    {name: 'Administrator',      
    options: [        
        { 
            name: 'Dodaj now lini',          
            descriptions: [option_admin_dodaj_linie]
        },
        {
          name: 'Usu lini',
          descriptions: [option_admin_usun_linie]
        },
        {
          name: 'Edytuj lini',
          descriptions: [option_admin_edytuj_linie]
        },
        {
          name: 'Dodaj nowy autobus',
          descriptions: [option_admin_dodaj_autobus]
        },
        {
          name: 'Usu autobus',
          descriptions: [option_admin_usun_autobus]
        },
        {
          name: 'Dodaj nowy kurs',
          descriptions: [option_admin_dodaj_kurs]
        },
        {
          name: 'Usu kurs',
          descriptions: [option_admin_usun_kurs]
        },
        {
          name: 'Dodaj nowy przystanek',
          descriptions: [option_admin_dodaj_przystanek]
        },
        {
          name: 'Usu przystanek',
          descriptions: [option_admin_usun_przystanek]
        },
        {
          name: 'Dodaj nowego uytkownika',
          descriptions: [option_admin_dodaj_uzytkownika]
        },
        {
          name: 'Usu uytkownika',
          descriptions: [option_admin_usun_uzytkownika]
        }
      ]
    },
    {name: 'Kierowca',      
    options: [        
        { 
            name: 'Okrel swoj dyspozycj',          
            descriptions: [option_kierowca_dyspozycja]
        },
        {
          name: 'Sprawd swj grafik',
          descriptions: [option_kierowca_grafik]
        }
      ]
    },
    {name: 'Dyspozytor',      
    options: [        
        { 
            name: 'Dodaj realizacj kursu',          
            descriptions: [option_dyspozytor_dodaj_realizacje]
        },
        {
          name: 'Usu realizacj kursu',
          descriptions: [option_dyspozytor_usun_realizacje]
        }
      ]
    }
];

const handleSelectSection = (sectionName) => {
    if (selectedSection === sectionName) {
      setSelectedSection(null);
    } else {
      setSelectedOption(null);
      setSelectedSection(sectionName);
    }
  };
  
  const handleSelectOption = (optionName) => {
    if (selectedOption === optionName) {
      setSelectedOption(null);
    } else {
      setSelectedOption(optionName);
    }
  };
  

return (
    <div>
      {sections.map((section) => (
        <div className='dropdownContainer' key={section.name}>
          <h3 className='dropdownTitle' onClick={() => handleSelectSection(section.name)}>
            {section.name}
          </h3>
          {selectedSection === section.name &&
            section.options.map((option) => (
              <div className='dropdownOptionsContainer' key={option.name}>
                <p className='dropdownOptionsName' onClick={() => handleSelectOption(option.name)}>
                  {option.name}
                </p>
                {selectedOption === option.name && (
                  <ul className='dropdownDescriptions'>
                    {option.descriptions.map((description) => (
                      <li key={description}>{description}</li>
                    ))}
                  </ul>
                )}
              </div>
            ))}
        </div>
      ))}
    </div>
  );

}
export default HelpDropdownList;
